export default "import { Radios, TextInput } from '@components';\nimport { Component } from 'solid-js';\nimport { useFormContext } from './context';\n\nexport const Step1: Component = () => {\n  const { formHandler } = useFormContext();\n\n  return (\n    <div class=\"row gy-3\">\n      <div class=\"col-12\">\n        <h3>Personal info:</h3>\n      </div>\n      <div class=\"col-12\">\n        <TextInput\n          label=\"First Name\"\n          name=\"step1.firstName\"\n          formHandler={formHandler}\n        />\n      </div>\n      <div class=\"col-12\">\n        <TextInput\n          label=\"Second Name\"\n          name=\"step1.secondName\"\n          formHandler={formHandler}\n        />\n      </div>\n      <div class=\"col-12\">\n        <Radios\n          label=\"Gender\"\n          name=\"step1.gender\"\n          options={[\n            { value: 'male', label: 'Male' },\n            { value: 'female', label: 'Female' },\n            { value: 'other', label: 'Other' },\n          ]}\n          formHandler={formHandler}\n        />\n      </div>\n    </div>\n  );\n};\n"